$accent-color: #2196f3;
$btn-color: #ffffff;
$input-color: #777777;
$btn-hover-bgcolor: #188ce8;

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

.list {
    margin: 0;
    padding: 0;
    list-style: none;
}

.link {
    display: block;
    text-decoration: none;
}

img {
    display: block;
    max-width: 100%;
    height: auto;
}

body {
    font-style: normal;
    font-family: 'Roboto', sans-serif;
    color: #fff;
    font-weight: 600;
    font-size: 20px;
    background-color: #e5e8e9;
    background-size: cover;
}

.container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding-right: 30px;
    padding-left: 30px;
}

.search-form {
    display: flex;
    justify-content: center;
    margin-top: 100px;
    margin-bottom: 20px;
}

.search-form__input {
    margin-right: 30px;
    width: 500px;
    border: 1px solid rgba(255, 255, 255, 0.8);
    box-sizing: border-box;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.15));
    border-radius: 10px;
    background-color: inherit;
    padding-top: 15px;
    padding-bottom: 15px;
    padding-left: 16px;
    height: 50px;
    
}

.search-form__input:focus {
    outline-color: $accent-color;
}

.search-form__input::placeholder{
    color: $input-color;
}

.btn {
    min-width: 200px;
    height: 50px;
    background-color: $accent-color;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
    border: none;
    border-radius: 10px;

    font-weight: 700;
    font-size: 16px;
    line-height: 1.87;
    text-align: center;
    letter-spacing: 0.06em;
    color: $btn-color;
}

.btn:hover,
.btn:focus {
    background-color: $btn-hover-bgcolor;
    cursor: pointer;
    outline-color: $accent-color;
}

.load-more {
    margin-top: 20px;
}

.gallery {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}

.photo-card {
    width: 100%;
    overflow: hidden;

    display: flex;
    flex-direction: column;
    justify-content: center;
}

.photo-card__item {
    display: flex;
    flex-direction: column;
    
    margin: 15px;
    width: calc((100% - 8*15px)/4);
    border-radius: 10px;
    overflow: hidden;
}

.photo-image {
    display: block;
    height: 100%;
    width: 100%;
    object-fit: cover;
    height: 15vw;
}

.stats {
    display: flex;
    justify-content: center;
    margin-top: 10px;
    
}

.stats-item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: $accent-color;
}

.stats-item:not(:last-child) {
    margin-right: 20px;
}

.material-icons {
    display: flex;
    justify-content: center;
    margin-bottom: 10px;
}

